.profile__main {
    /* display: flex; */
    /* flex-direction: column; */
    align-items: center;
    z-index: 100;
    background-color: white;
}

.profile__first {
    display: flex;
    align-items: center;
    background-color: white;
    height: 200px;
    width: 80%;
    margin-left: 10%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
}

.profile__first > img {
    height: 140px;
    object-fit: contain;
    padding-left: 20px;
}

.profile__first > h4 {
    padding-left: 20px;
    height: inherit;
    padding-top: 30px;
}

.wishme {
    padding-left: 4px;
}

.profile__firstHalf {
    background-color: lightgray;
    object-fit: contain;
    justify-content: flex-end;
    align-items: flex-end;
}

.profile__second {
    height: 50px;
    justify-content: flex-start;
    display: flex;
    align-items: center;
    width: 80%;
    z-index: 100;
    margin-left: 10%;
    background-color: white;
    margin-top: 30px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 20px 0 rgba(0, 0, 0, 0.1);
}

.profile__second > h5 {
    padding-left: 30px;
    cursor: pointer;
}

.profile__second > h5:hover {
    border-bottom: 3px solid lightgreen;
}

.profile__third {
    height: 500px;
    /* justify-content: flex-start; */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 80%;
    z-index: 100;
    margin-left: 10%;
    background-color: white;
    margin-top: 30px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 20px 0 rgba(0, 0, 0, 0.1);
}

.profile__third > h4, h6 {
    padding-top: 15px;
    padding-left: 30px;
}

.profile__thirdHeading {
    width: 100%;
}